import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormBuilder, AbstractControl, FormControl } from '@angular/forms'
import { ValidateService } from '../../services/validate.service';
import { AuthService } from '../../services/auth.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router } from '@angular/router';


@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  @Input()
  name: AbstractControl;
  @Input()
  surname: AbstractControl;
  @Input()
  email: AbstractControl;
  @Input()
  password: AbstractControl;
  @Input()
  registerForm: FormGroup;

  constructor(
    fb: FormBuilder, 
    private validateService: ValidateService, 
    private flashMessagesService: FlashMessagesService,
    private authService: AuthService,
    private router: Router) {
    this.registerForm = fb.group({
      'name': [''],
      'surname': [''],
      'email': [''],
      'password': ['']
    });

    this.name = this.registerForm.controls['name'];
    this.surname = this.registerForm.controls['surname'];
    this.email = this.registerForm.controls['email'];
    this.password = this.registerForm.controls['password'];

   }

  ngOnInit() {
  }

  onRegisterSubmit(value: any){
    const user ={
      name: `${value.name} ${value.surname}`,
      email: value.email,
      password: value.password
    }

    //Validation
    if(!this.validateService.validateRegister(user)){
      this.flashMessagesService.show('Uzupełnij wszystkie pola.', {cssClass: 'alert-danger', timeout: 3000});
      return false;
    }
    if(!this.validateService.validateEmail(user.email)){
      this.flashMessagesService.show('Wprowadź poprawny email np.: jkowalski@przyklad.com .', {cssClass: 'alert-danger', timeout: 3000});
      return false;
    }
    
    //Register user
    this.authService.registerUser(user).subscribe(data => {
      if(data.success){
        this.flashMessagesService.show('Zarejestrowałeś się, możesz teraz zalogować się do swojego konta.', {cssClass: 'alert-success', timeout: 3000});
        setTimeout(() => 
        {
          this.router.navigate(['/login']);
        },
        3000);
        
      }
      else{
        this.flashMessagesService.show('Problem z rejestracją.', {cssClass: 'alert-danger', timeout: 3000});
        this.router.navigate(['/register']);
      }
    });
  }
}



