import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormBuilder, AbstractControl, FormControl } from '@angular/forms';
import { ValidateService } from '../../services/validate.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { AuthService } from '../../services/auth.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  @Input()
  email: AbstractControl;
  @Input()
  password: AbstractControl;
  @Input()
  loginForm: FormGroup;

  constructor(fb: FormBuilder,
  private validateService: ValidateService,
  private flashMessagesService: FlashMessagesService,
  private authService: AuthService,
  private router: Router) {
    this.loginForm = fb.group({
      'email': [''],
      'password': ['']
    });
  }

  ngOnInit() {
  }

  onLoginSubmit(value: any){
    const user = {
      email: value.email,
      password: value.password
    }
        //Validation
    if(!this.validateService.validateLogin(user)){
          this.flashMessagesService.show('Uzupełnij wszystkie pola.', {cssClass: 'alert-danger', timeout: 3000});
          return false;
    }
    if(!this.validateService.validateEmail(user.email)){
      this.flashMessagesService.show('Wprowadź poprawny email np.: jkowalski@przyklad.com .', {cssClass: 'alert-danger', timeout: 3000});
      return false;
    }
  
    //Register user
    this.authService.authenticateUser(user).subscribe(data => {
      console.log(data);
      if (data.success) {
        this.authService.storeUserData(data.token, data.user);
        this.flashMessagesService.show('Udało Ci się zalogować.', {cssClass: 'alert-success', timeout: 1000});
        setTimeout(() => 
        {
          this.router.navigate(['/dashboard']);
        },
        1000);
      }
      else {
        this.flashMessagesService.show(data.msg, { cssClass: 'alert-danger', timeout: 4000 });
        this.router.navigate(['/login']);
      }
    });
  }

}



